A,CACA,UAAA,CACA,SAAA,CAEA,wDACE,OAAA,CACA,sBAAA,CACA,SAAA,CACA,iBAAA,CACA,cAAA,CACA,kBAAA,CACA,aAAA,CAGF,8CTnEF,YAZU,CAaV,kBSmEiC,CT/DT,6BS+DoD,CTzDtD,kBSyD6B,CTnDnD,gBAzBO,CA0BP,aAzBS,CA0BT,WAzBO,CS2EH,eAAA,CAEA,qDACE,SAAA,CACA,iBAAA,CACA,cAAA,CACA,kBAAA,CACA,SAAA,CACA,YAAA,CACA,WAAA,CACA,eAAA,CACA,WAAA,CAGF,sDACE,aAAA,CAGF,sDACE,aAAA,CC3FR,KACI,QAAA,CACA,wBAAA,CACA,WAAA,CAGJ,EACI,+BAAA,CAIJ,SACI,aAAA,CACA,2BAAA,CAEA,yBAJJ,SAKQ,gBAAA,CAAA,CAGJ,uBACI,WAAA,CAEA,yBAHJ,uBAIQ,wBAAA,CAAA,CAKZ,KACI,iBAAA,CACA,SAAA,CAMJ,OAEI,cAAA,CACA,KAAA,CACA,MAAA,CACA,YAAA,CACA,UAAA,CACA,aAAA,CAIJ,OACI,uBAAA,CAIJ,mBACI,YAAA,CAIJ,KACI,kBAAA,CACA,SAAA","sourcesContent":["@font-face {\r\n  font-family: \"Rossanova\";\r\n  src: url(\"font/Rossanova.woff2\") format(\"woff2\"),\r\n  url(\"font/Rossanova.woff\") format(\"woff\");\r\n  font-display: swap;\r\n}\r\n@font-face {\r\n  font-family: \"Chapaza\";\r\n  src: url(\"font/Chapaza.woff2\") format(\"woff2\"),\r\n  url(\"font/Chapaza.woff\") format(\"woff\");\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"Story\";\r\n  src: url(\"font/Judson-Regular.woff2\") format(\"woff2\"),\r\n  url(\"font/Judson-Regular.woff\") format(\"woff\");\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"Canela\";\r\n  src: url(\"font/Canela-Bold.woff2\") format(\"woff2\");\r\n  font-weight: bold;\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"Canela\";\r\n  src: url(\"font/Canela-Medium.woff2\") format(\"woff2\");\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"FoundersGroteskLight\";\r\n  src: url(\"font/Founders_Grotesk_Light.woff2\") format(\"woff2\");\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"FoundersGroteskMedium\";\r\n  src: url(\"font/Founders_Grotesk_Medium.woff2\") format(\"woff2\");\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"Chalk\";\r\n  src: url(\"font/Chalk-Regular.woff2\") format(\"woff2\");\r\n  font-display: swap;\r\n}\r\n",".cursor {\r\n  position: fixed;\r\n  left: 0;\r\n  top: 0;\r\n  pointer-events: none;\r\n  opacity: 0;\r\n\r\n  // Don't show cursor on mobile devices\r\n  @media (max-width: map-get($breakpoints, breakpoint-tablet)) {\r\n    display: none !important\r\n  }\r\n}\r\n\r\n\r\n.cursor--small {\r\n  position: absolute;\r\n  transform: translate(-50%, -50%);\r\n  padding: 3px;\r\n  border-radius: 50%;\r\n  z-index: 12000;\r\n  border: .1px solid map-get($palettes, orange);\r\n}\r\n\r\n.cursor--canvas {\r\n  width: 100vw;\r\n  height: 100vh;\r\n  z-index: 13000;\r\n}\r\n","@import 'utils/vars';\r\n\r\n.navbar {\r\n  height: 60px;\r\n  width: 100%;\r\n  position: fixed;\r\n  z-index: 10000;\r\n  font-family: Chapaza;\r\n  background-color: map-get(map-get($palettes, yellow), light);\r\n  font-size: 1.15em;\r\n\r\n  @media (max-width: map-get($breakpoints, breakpoint-mobileL)) {\r\n    font-size: 1em;\r\n  }\r\n\r\n  @media (min-width: map-get($breakpoints, breakpoint-tablet)) {\r\n    width: map-get($sidebar, width);\r\n    height: 100%;\r\n  }\r\n\r\n  /* Progress bar */\r\n  .progress-bar {\r\n    background-color: map-get(map-get($palettes, red), base);\r\n    height: 3px ;\r\n    width: 100%;\r\n    position: fixed;\r\n\r\n    @media (min-width: map-get($breakpoints, breakpoint-tablet)) {\r\n      height: 100% ;\r\n      width: 3px;\r\n    }\r\n\r\n    @media (max-width: map-get($breakpoints, breakpoint-tablet)) {\r\n      display:  none;\r\n    }\r\n  }\r\n\r\n\r\n  /* Nav */\r\n  nav {\r\n    flex: 1;\r\n    height: 100%;\r\n    width: 100%;\r\n    @include flexbox($direction: column);\r\n  }\r\n\r\n  .menu-container {\r\n    flex: 1;\r\n    @include flexbox($direction: row, $alignItems: center, $justifyContent: center, $grow: 1);\r\n\r\n    @media (min-width: map-get($breakpoints, breakpoint-tablet)) {\r\n      @include flexbox($direction: column, $alignItems: center, $justifyContent: center, $grow: 1);\r\n    }\r\n  }\r\n\r\n  .menu-container_submenu {\r\n    width: 100%;\r\n    height: 100%;\r\n    @include flexbox($alignItems: center, $justifyContent: center, $grow: 1);\r\n    flex: 1;\r\n  }\r\n\r\n  /* Menu animation */\r\n  /* Menu styles */\r\n  .menu__item {\r\n    display: block;\r\n    margin: 0 1em;\r\n  }\r\n\r\n  .menu__link {\r\n    position: relative;\r\n    margin: 0 1em;\r\n    padding-right: 0;\r\n    padding-left: 0;\r\n    color: #b5b5b5;\r\n    -webkit-transition: color 0.4s;\r\n    transition: color 0.4s;\r\n    text-decoration: none;\r\n    cursor: none;\r\n\r\n    // Rotating on all browsers\r\n    -webkit-transform: none;\r\n    -moz-transform: none;\r\n    -ms-transform: none;\r\n    -o-transform: none;\r\n    filter: none;\r\n\r\n    @media (min-width: map-get($breakpoints, breakpoint-tablet)) {\r\n      -webkit-transform: rotate(-90deg);\r\n      -moz-transform: rotate(-90deg);\r\n      -ms-transform: rotate(-90deg);\r\n      -o-transform: rotate(-90deg);\r\n      filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);\r\n    }\r\n  }\r\n\r\n  .menu__item--current .menu__link,\r\n  .menu__item--current .menu__link:hover,\r\n  .menu__item--current .menu__link:focus {\r\n    color: map-get(map-get($palettes, red), dark);\r\n  }\r\n\r\n  .menu__item--current .menu__link::after,\r\n  .menu__item--current .menu__link::before {\r\n    -webkit-transform: scale3d(1, 1, 1);\r\n    transform: scale3d(1, 1, 1);\r\n  }\r\n\r\n  .menu__item--current .menu__link::before {\r\n    -webkit-transition-delay: 0s;\r\n    transition-delay: 0s;\r\n  }\r\n\r\n  .menu__link:hover,\r\n  .menu__link:focus {\r\n    color: #b5b5b5;\r\n  }\r\n\r\n  .menu__link:hover::before,\r\n  .menu__link:focus::before {\r\n    -webkit-transform: scale3d(1, 1, 1);\r\n    transform: scale3d(1, 1, 1);\r\n    -webkit-transition-delay: 0s;\r\n    transition-delay: 0s;\r\n  }\r\n\r\n  .menu__link::before,\r\n  .menu__link::after {\r\n    content: '';\r\n    position: absolute;\r\n    bottom: -10px;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 3px;\r\n    -webkit-transform: scale3d(0, 1, 1);\r\n    transform: scale3d(0, 1, 1);\r\n    -webkit-transform-origin: center left;\r\n    transform-origin: center left;\r\n    -webkit-transition: transform 0.4s cubic-bezier(0.22, 0.61, 0.36, 1);\r\n    transition: transform 0.4s cubic-bezier(0.22, 0.61, 0.36, 1);\r\n  }\r\n\r\n  .menu__link::before {\r\n    background: #b5b5b5;\r\n    -webkit-transition-delay: 0.4s;\r\n    transition-delay: 0.4s;\r\n  }\r\n\r\n  .menu__link::after {\r\n    background: map-get(map-get($palettes, red), dark);\r\n  }\r\n}\r\n","/**\r\n* FLEXBOX\r\n* A mixin to rule complex flexbox layout\r\n* @author nicolacastellanidev@gmail.com\r\n* https://dev.to/nicolalc/a-mixin-to-rule-them-all-flexbox-driven-layout-mixin-1h43\r\n*/\r\n@mixin flexbox(\r\n  $display: flex,\r\n  $direction: row,\r\n  $placeContent: null,\r\n  $placeItems: null,\r\n  $wrap: nowrap,\r\n  $shrink: 0,\r\n  $grow: 0,\r\n  $alignContent: null,\r\n  $justifyContent: null,\r\n  $alignItems: null,\r\n  $justifyItems: null) {\r\n\r\n  display: $display;\r\n  flex-direction: $direction;\r\n\r\n  @if $placeContent == null {\r\n    @if $alignContent   { align-content: $alignContent; }\r\n    @if $justifyContent { justify-content: $justifyContent; }\r\n  } @else {\r\n    place-content: $placeContent;\r\n  }\r\n\r\n  @if $placeItems == null {\r\n    @if $alignItems   { align-items: $alignItems; }\r\n    @if $justifyItems { justify-items: $justifyItems; }\r\n  } @else {\r\n    place-items: $placeItems;\r\n  }\r\n\r\n  flex-wrap: $wrap;\r\n  flex-shrink: $shrink;\r\n  flex-grow: $grow;\r\n}\r\n\r\n// --------------------------------\r\n\r\n// ==============================\r\n// Font size mixin\r\n// from https://codepen.io/jakob-e/pen/yVyYzv\r\n// ==============================\r\n\r\n//  default font scale bounding\r\n$font-lock-min:  400px !default;\r\n$font-lock-max: 1000px !default;\r\n\r\n//  font-size\r\n@mixin font-size(\r\n  $min,                         // font-size\r\n  $max: null,                   // optional maximum font size\r\n  $lock-min: $font-lock-min,  // optional minimum viewport width\r\n  $lock-max: $font-lock-max   // optional maximum viewport width\r\n){\r\n  & { font-size: $min; }\r\n  @if $max {\r\n    @media (min-width: $lock-min) {\r\n      & {\r\n        font-size: calc(\r\n                #{$min} +\r\n                #{num($max) - num($min)} *\r\n                (100vw - #{$lock-min}) /\r\n                #{num($lock-max) - num($lock-min)}\r\n        );\r\n      }\r\n    }\r\n    @media (min-width: $lock-max){\r\n      & { font-size: $max; }\r\n    }\r\n  }\r\n}\r\n\r\n//  strip units helper function\r\n@function num($input){\r\n  @return $input/($input * 0 + 1);\r\n}\r\n","@import 'utils/vars';\r\n\r\n.progress_line {\r\n  width: 100%;\r\n  max-width: 800px;\r\n  height: 1px;\r\n  position: relative;\r\n  display: inline-block;\r\n  background-color: rgba(255,255,255,1);\r\n  transform-origin: left center;\r\n}\r\n\r\n.story {\r\n\r\n    .title--text_span {\r\n      line-height: initial;\r\n      color: black;\r\n    }\r\n\r\n    section {\r\n      height: 100vh;\r\n      display: grid;\r\n      place-items: center;\r\n    }\r\n\r\n    &__section {\r\n      font-family: \"Story\", serif;\r\n    }\r\n\r\n    :nth-child(1) {\r\n      .title--text {\r\n        opacity: 0;\r\n        margin: 28vh auto auto 9vw;\r\n        text-align: right;\r\n        font-weight: 100;\r\n      };\r\n\r\n      .title--text_span {\r\n        line-height: initial;\r\n        color: whitesmoke;\r\n      }\r\n\r\n      @media (max-width: map-get($breakpoints, breakpoint-tablet)) {\r\n        .title--text {\r\n          margin: auto;\r\n          text-align: center;\r\n        }\r\n      }\r\n    }\r\n\r\n    :nth-child(2) {\r\n      .title--text {\r\n        opacity: 0;\r\n        margin: auto 7vw 23vh auto;\r\n        text-align: left;\r\n        font-weight: 100;\r\n      };\r\n\r\n      .title--text_spa